<template>
  <div class="register">
    <van-nav-bar
      title="注册"
      left-text="登录"
      right-text="关闭"
      @click-left="logins('Login')"
      @click-right="goMenu"
    />
    
    <div class="logo">
      <img src="../assets/images/a.png" class="auto-img"/>
    </div>

    <van-form class="form" @submit="register">
        <van-field
        type="text"
        v-model="userinfo.phone"
            label="手机号"
            placeholder="请输入11位手机号"
            left-icon="phone-o"
            maxlength='11'
        >
        </van-field>

          <van-field
          type="text"
            label="昵称"
            v-model="userinfo.nickName"
            placeholder="字母数字_组合(1-10位)"
            left-icon="user-o"
             maxlength="10"
        />
             <van-field
                :type="addNum?'text':'password'"
                label="密码"
                v-model="userinfo.password"
                placeholder="字母数字_组合(6-16位)"
                left-icon="lock"
                :right-icon="addNum?'eye-o':'closed-eye'"
                @click-right-icon="passWd"
                maxlength="16"
            >

             </van-field>

        <div class="sub_btn">
            <van-button round block type="info" native-type="submit">
            注册
            </van-button>
        </div>
   </van-form>

      <!-- <van-dialog title="阿里路亚">
      </van-dialog> -->
  </div>
</template>

<script>

  //导入验证表单模块
  import validForm from '../assets/js/validForm'

import { createNamespacedHelpers } from "vuex";

const { mapState, mapMutations } = createNamespacedHelpers("homeModules");

  export default {
      data(){
        return {
          userinfo: {
            phone: '',
            nickName: '',
            password: ''
          },
          addNum: false
        }
      },
      computed:{
        ...mapState(["petsStyleData","mySearchData","communityData","myLikeData","myLikePostData","saveUerComData"])
      },
      created (){
        if(localStorage.getItem('pestData')==null){
          // let petsStyleData=[];

          let obj={
            petsStyleData: this.petsStyleData,
            mySearchData: this.mySearchData,
            communityData: this.communityData,
            myLikeData: this.myLikeData,
            myLikePostData: this.myLikePostData,
            saveUerComData: this.saveUerComData
          };

          localStorage.setItem('pestData',JSON.stringify(obj));
        }
      },
      methods:{
        logins(name){
           this.$router.push({name})
        },
        passWd(){
          this.addNum=!this.addNum;
        },
        //注册
        register(){
          //进行验证表单
          
          if (validForm.validUserForm(this.userinfo, this)) {
            

            //复制对象
            let data = Object.assign({}, this.userinfo);

            //请求密钥
            data.appkey = 'U2FsdGVkX19WSQ59Cg+Fj9jNZPxRC5y0xB1iV06BeNA=';

            //开启加载提示
            this.$toast.loading({
              message: '加载中...',
              forbidClick: true,

              //延迟自动关闭加载提示，如果该值为0，则不会自动关闭
              duration:0
            });

            //发起注册请求
            this.axios({
              method: 'POST',
              url: 'http://www.kangliuyong.com:10002/register',

              //post传递参数需要写在data
              data
            }).then(result => {
              //关闭加载提示
              this.$toast.clear();
              if(result.data.code==100){
                this.$router.push({name: 'Login'});
              }else{
                this.$toast.fail(result.data.msg);
              }
              // this.$router.push({name: 'Login'});
              
            }).catch(err => {
              //关闭加载提示
              this.$toast.clear();
              
            })
          }
        },
         goMenu(){
          this.$router.push({name: 'HomePage'});
        }
      }
  }
</script>

<style lang="less" scoped>
   .logo{
    width: 120px;
    height: 120px;
    margin: 50px auto;
  }
  .sub_btn{
    margin: 50px;
  }
</style>